@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Layout */
body {
   margin: 0;
  padding: 0;
}

.container {
  display: flex;
  flex-direction: column;

}

.main {
  flex-grow: 1;
  margin-top: -7px;
  display: flex;
  flex-direction: row;
  /* border-style: dashed; */
}

.left {
  width: 25%;
}

.mainContent {
  flex-grow: 1;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;

  min-height: 45vh;
  width: 50%;

  background-color: rgb(255, 255, 255);
  background-color: rgba(0,0,0, 0.4);
  border-radius: 3px;

}

.right {
  width: 25%;

}

.footer {

}

/* Elements */

.bg {
  /* Set rules to fill background */
  min-height: 100%;
  min-width: 1024px;
  opacity: 75%;
  /* Set up proportionate scaling */
  width: 100%;
  height: auto;

  /* Set up positioning */
  position: fixed;
  top: 0;
  left: 0;
  z-index:  -1;
  filter: blur(5px);
  -webkit-filter: blur(5px);
}

@media screen and (max-width: 1024px) { /* Specific to this particular image */
  img.bg {
    left: 50%;
    margin-left: -512px;   /* 50% */

  }
}

h1, h2, p {
  color: white;
  margin: 0px;
  padding: 0px;
  font-family: 'Roboto', sans-serif;
}


.title {
  font-size: 2.2em;
  padding-top: 25px;
}
/* input {
  border-color: transparent;
  flex: 1;
  height: 3.5vh;
  width: auto;
} */

.secs {
  font-size: 3em;
}

.startCh {
  font-size: 3.7em;
  margin-right: 30px;
}

.scoreDiv {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 24px;

}

.inpDiv {
  border-color: transparent;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin-bottom: 21px;
}

.form {
  width: 100%;
  display: flex;
  align-items: stretch;

}

form {
  width: 100%;
  margin-top: 10px;
}

input {
  border-color: transparent;
  flex: 1 1;
  height: 3.5vh;
  width: 99%;


  text-align: center;
  font-size: 10;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
}

input[type='text'] { font-size: 24px; }

textarea:focus, input:focus{
  outline: none;
}

*:focus {
  outline: none;
}

.submitButton {
  margin-top: 20px;
  margin-bottom: 16px;
  background-color:#33be69;
  border-radius:3px;

  font-family: 'Roboto', sans-serif;
  font-size:16px;
  font-weight:bold;
  padding:13px 24px;
  color: white;
  text-decoration:none;
  max-width: 34vh;
  border: 2px solid #33be69
  }


.wrongTxt {
  color: rgb(255, 71, 71);
  font-weight: bold;
  padding: 1;
}

.correctTxt {
  color: rgb(50, 214, 50);
  font-weight: bold;

  padding: 5;
}

.greenTxt {
  color: rgb(50, 214, 50);
}

.display-linebreak {
  white-space: pre-line;
}

.scoreTxt {
  font-size: 26px;
}

/*info box*/
.blue-border{
  border: solid 3px #2e94f5;
  background-color: rgba(32, 179, 216, 0.4);
  padding: 5px;
  margin-bottom: 5px;
  display: flex;
  /* align-items: center; */
  justify-content: center;
  width: auto;
}



